import React, { FC, useState } from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Grid from '@material-ui/core/Grid';
import { makeStyles, Theme } from '@material-ui/core/styles';
import clsx from 'clsx';

import { Components } from '@hims/core';

import { IAddPlanTypeModal } from './IAddPlanTypeModal';

const InputTextField = Components.UI.InputTextField.InputTextField;

const useStyles = makeStyles((theme: Theme) => ({
	dialog: {},
	dialogAction: {
		marginBottom: theme.spacing(2),
		justifyContent: 'center'
	},
	container: {
        paddingRight:'15px',
        paddingLeft:'15px',
        maxWidth: '500px'
	},
	buttons: {
		minWidth: 115,
		marginRight: '20px'
	}
}));

export type IDataField = IAddPlanTypeModal.IDataField;

export const AddPlanTypeModal: FC<IAddPlanTypeModal.IProps> = props => {
	const classes = useStyles();

	const [name, setName] = useState<string>('');
	const [nameError, setNameError] = useState<boolean>(false);
	const [nameErrorMessage, setNameErrorMessage] = useState('');
	// const [code, setCode] = useState<string>('');
	// const [codeError, setCodeError] = useState<boolean>(false);
	const [iPrincipal, setIPrincipal] = useState<string>('0');
	const [iPrincipalError, setIPrincipalError] = useState<boolean>(false);
	const [iDependent, setIDependent] = useState<string>('0');
	const [iDependentError, setIDependentError] = useState<boolean>(false);
	const [requestedRate, setRequestedRate] = useState<string>('0');
	const [requestedRateError, setRequestedRateError] = useState<boolean>(false);

	const reset = () => {
		setName('');
		setNameError(false);
		setIPrincipal('0');
		setIPrincipalError(false);
		setIDependent('0');
		setIDependentError(false);
		setRequestedRate('0');
		setRequestedRateError(false);
		// setCode('');
		// setCodeError(false);
	};

	const handleClose = () => {
		reset();
		props.onClose();
	};

	const handleSubmit = () => {
		let error = false;
		if (name.length === 0) {
			setNameError(true);
			error = true;
		}

		const index = props.planTypeList.findIndex(item => 
		    item.name.toLowerCase() === name.toLowerCase()
		);

		if(index !== undefined && index !== -1){
			setNameError(true);
			setNameErrorMessage("Name already exists.");
			error = true;
		}

		if (parseFloat(iPrincipal) < 0) {
			setIPrincipalError(true);
			error = true;
		}

		if (parseFloat(iDependent) < 0) {
			setIDependentError(true);
			error = true;
		}

		if (parseFloat(requestedRate) <= 0 ) {
			setRequestedRateError(true);
			error = true;
		}

		
		if (!error) {
			const data: IDataField = {
				name: name,
				code: name.replace(/\s+/g, '').toUpperCase(),
				indicativeNumOfPrincipals: parseFloat(iPrincipal),
				indicativeNumOfDependents: parseFloat(iDependent),
				requestedRate: parseFloat(requestedRate),
			};

			reset();
			props.onSubmit(data);
			props.onClose();
		}
	};

	const numallowedpattern = new RegExp(/\D/);

	return (
		<Dialog
			open={props.isModalOpen}
			onClose={handleClose}
			aria-labelledby="form-dialog-title"
			maxWidth="sm"
			className={classes.dialog}
		>
			<div>
                <Grid container className={classes.container} justify="flex-start" alignItems="flex-start" direction="column">
                    <Grid item xs>
						<DialogTitle id="form-dialog-title">Add New Plan Type</DialogTitle>
					</Grid>
					<Grid item xs>
						<DialogContent>
							<Grid container spacing={2} justify="space-between" alignItems="flex-start">
								<Grid item xs={12}>
									<InputTextField
										label="Plan Type Name*"
										value={name}
										error={nameError}
										errorMessage={nameErrorMessage}
										onChange={(value: string) => {
											setName(value);
											setNameError(value.length === 0);
											setNameErrorMessage('');
										}}
									/>
								</Grid>
								<Grid item xs={6}>
									<InputTextField
										type="tel"
										label="Indicative No. of Principals"
										value={iPrincipal}
										error={iPrincipalError}
										onChange={(value: string) => {
											
											if(!numallowedpattern.test(value)){
												console.log(value)
												if(value.length===0)
												{
													value = '0';
												}
												else if(value.indexOf('0')===0 && value.length===1)
												{
													value = '0';
												}
												else if(value.indexOf('0')===0)
												{
													value = value.substring(1);
												}
												setIPrincipal(value);
												setIPrincipalError(parseInt(value) < 0);
											}
											
										}}
									/>
								</Grid>
								<Grid item xs={6}>
									<InputTextField
										type="tel"
										label="Indicative No. of Dependents"
										value={iDependent}
										error={iDependentError}
										onChange={(value: string) => {
											if(!numallowedpattern.test(value)){
											if(value.length===0)
											{
												value = '0';
											}
											else if(value.indexOf('0')===0 && value.length===1)
											{
												value = '0';
											}
											else if(value.indexOf('0')===0)
											{
												value = value.substring(1);
											}
											setIDependent(value); 
											setIDependentError(parseInt(value) < 0);
										}
										}}
									/>
								</Grid>
								<Grid item xs={6}>
									<InputTextField
										type="tel"
										label="Requested Rate*"
										value={requestedRate}
										error={requestedRateError}
										onChange={(value: string) => {
											if(!numallowedpattern.test(value)){
											if(value.length===0)
											{
												value = '0';
											}
											else if(value.indexOf('0')===0 && value.length===1)
											{
												value = '0';
											}
											else if(value.indexOf('0')===0)
											{
												value = value.substring(1);
											}
											setRequestedRate(value);
											setRequestedRateError(parseInt(value) < 0);
										}
										}}
									/>
								</Grid>
							</Grid>
						</DialogContent>
						<DialogActions className={clsx(classes.dialogAction)}>
							<Button onClick={handleClose} variant="outlined" color="default" className={classes.buttons}>
								Cancel
							</Button>
							<Button
								onClick={handleSubmit}
								variant="contained"
								color="primary"
								className={classes.buttons}
							>
								Add
							</Button>
						</DialogActions>
					</Grid>
				</Grid>
			</div>
		</Dialog>
	);
};

AddPlanTypeModal.defaultProps = {
	isModalOpen: false,
};
